<script>

function haveDateTime(str){
  let reg = /[1-2][0-9]{3}-[0-9]{1,2}-[0-9]{1,2} [0-2][0-9]:[0-5][0-9]:[0-5][0-9]/g;
  if(str.match(reg) == null){
    return null;
  }else{
    let res = {
      time:str.match(reg),
      description:str.split(reg)
    }
    return res;
  }
}

export default {
  name: 'OrderEdit',
  data() {
    return {
      token:'',
      user_name:'Unknown',
      pageLoading:true,
      isLoading:false,
      canDo:{},
      doList:['order_view','order_new','order_edit','update','getProcessList','process_update'],
      edit:false,
      newOrder:false,
      stepList:[
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''}
      ],
      trouble_class:[{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''}],
      trouble_reason:{
        '':[
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''}
        ],
        '':[
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''}
        ],
        '':[
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''}
        ],
        '':[
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''},
          {label:'',value:''}
        ]
      },
      business_type:[
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''}
      ],
      roomType:[
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
      ],
      area:[
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
      ],
      major:[
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''},
        {label:'',value:''}
      ],
      description_holder:{
        '':'',
        '':'',
        '':'',
        '':''
      },
      id:'',
      order:{},
      processList:[],
      timeCounter:undefined,
    }
  },
  created: function() {
    this.pageLoading = true;
    this.token = this.$cookies.get('user_token');
    this.user_name = this.$cookies.get('user_name');
    this.id = this.$route.query.id;
    let self = this;
    if(this.id != undefined){
      this.assess_query(this.doList).then(function(){
        self.edit = (self.$route.query.edit == 'true') && self.canDo.order_edit
        self.getData();
      })
    }else{
      this.assess_query(this.doList).then(function(){
        self.edit = true;
        self.newOrder = true;
        self.pageLoading = false;
      });
    }
  },
  methods: {
    getData: function() {
      let self = this;
      let data = new FormData();
      data.append('token', this.token);
      data.append('id',this.id);
      this.axios
        .post('http://' + this.$global_msg.HOST + 'scripts/order/getOrderById.php', data)
        .then(function(res){
          if(res.data.status == 'success'){
            self.order = res.data;
            document.title = res.data.name;
            self.getProcessList();
          }else{
            self.$message.error("[getData]Error:"+res.data.errMsg);
          }
          self.pageLoading = false;
        })
        .catch(function(e){
          console.log("[getData]"+e);
          self.$message.error("[getData]Network Error:"+e);
          self.pageLoading = true;
        })
    },
    getProcessList:function(){
      let self = this;
      let data = new FormData();
      data.append('token', this.token);
      data.append('id',this.id);
      this.axios
        .post('http://' + this.$global_msg.HOST + 'scripts/order/get_process_list_by_order_id.php', data)
        .then(function(res){
          if(res.data.status == 'success'){
            self.processList = res.data.result
            self.refreshProcessList();
          }else{
            self.$message.error("[getProcessList]error:"+res.data.errMsg);
          }
        })
        .catch(function(e){
          console.log("[getProcessList]"+e);
          self.$message.error("[getProcessList]Network Error:"+e);
        })
    },
    refreshProcessList:function(){
      for(let i in this.processList){
        this.processList[i].list_order = i
      }
      if(this.processList > 0){
        this.order.process = this.processList[this.processList.length - 1]
      }
    },
    deleteProcess:function(index){
      let self = this;
      this.$confirm('?','',{
        type:'warning'
      }).then(function(){
        self.processList.splice(index,1)
        self.refreshProcessList();
      }).catch(function(){})
    },
    addProcess:function(){
      this.processList.push({
        process_id:'New Process',
        order_id:this.id,
        time:'',
        description:'',
        mark:'',
        list_order:-1
      })
      this.refreshProcessList();
    },
    autoAddProcess: function(obj) {
      let self = this;
      let res = haveDateTime(obj.description)
      if (res) {
        this.$confirm("", '', {
            type: 'info'
          })
          .then(function() {
            let index = Number(obj.list_order);
            console.log(res);
            if (res.time.length > res.description.length) {
              self.$message.error('')
            } else {
              if (res.time.length < res.description.length) {
                if (res.description[0] != "") {
                  self.processList[index].description = res.description[0].trim();
                  res.description.splice(0, 1);
                  index++;
                } else {
                  res.description.splice(0, 1);
                  self.processList.splice(index, 1);
                }
              }
              for (let i = 0; i < res.description.length; i++) {
                let fd = new Date(res.time[i]).Format("yyyy-MM-dd hh:mm:ss");
                let newProcess = {
                  process_id: "newProcess",
                  order_id: self.id,
                  description: res.description[i].trim(),
                  time: fd,
                  mark: "",
                  list_order: index + ""
                }
                self.processList.splice(index, 0, newProcess);
                index++;
              }
              self.refreshProcessList();
            }
          }).catch(function(){})
      }
    },
    deleteProcessList:function(callback){
      let self = this;
      let data = new FormData();
      data.append('token', this.token);
      data.append('id',this.id);
      this.axios
        .post('http://' + this.$global_msg.HOST + 'scripts/order/delete_process_by_order_id.php', data)
        .then(function(res){
          if(res.data.status == 'success'){
            callback();
          }else{
            self.$message.error("[deleteProcessByOrderId]error:"+res.data.errMsg);
          }
        })
        .catch(function(e){
          console.log("[deleteProcessByOrderId]"+e);
          self.$message.error("[deleteProcessByOrderId]Network Error:"+e);
        })
    },
    updateProcessList:function(callback){
      let self = this;
      let data = new FormData();
      data.append('token', this.token);

      let json = {sum:0,data:[]};
      for(let i in this.processList){
        let arr = [];
        arr.push(this.processList[i].process_id);
        arr.push(this.processList[i].order_id);
        arr.push(this.processList[i].time);
        arr.push(this.processList[i].description);
        arr.push(this.processList[i].list_order);
        arr.push(this.processList[i].mark);
        json.data.push(arr)
      }
      data.append('DATA',JSON.stringify(json));
      self.deleteProcessList(function(){
        if(self.processList.length > 0){
          self.axios
          .post('http://' + self.$global_msg.HOST + 'scripts/order/add_process_list.php', data)
          .then(function(res){
            if(res.data.status == 'success'){
              callback()
            }else{
              self.$message.error("[updateProcessList]error:"+res.data.errMsg);
            }
          })
          .catch(function(e){
            console.log("[updateProcessList]"+e);
            self.$message.error("[updateProcessList]Network Error:"+e);
          })
        }else{
          callback()
        }
      })
    },
    parseProcess:function(row,column,event){
      if(!this.edit){
        let text = row.time + ' ' + row.description
        this.$copyText(text).then(res => {
            alert('');
          },
          err => {
            alert('');
            console.log(err);
          }
        );
      }
    },
    getOrderData:function(){
      let paramList = ['id','name','start_time','end_time','step','trouble_symptom','link_id','process','circuit_number','contact_number','contact_name','area','is_trouble','is_remote','trouble_class','trouble_reason','business_type','remark','major','roomName','roomType','reasonDescription','hiddenDanger'];
      let data = new FormData();
      for(let i in paramList){
        data.append(paramList[i],this.order[paramList[i]]==undefined?'':this.order[paramList[i]])
      }
      return data;
    },
    orderUpdate:function(callback){
      let self = this;
      let data = this.getOrderData();
      data.append('token', this.token);

      this.axios
        .post('http://' + self.$global_msg.HOST + 'scripts/order/update.php', data)
        .then(function(res){
          if(res.data.status == 'success'){
            callback();
          }else{
            self.$message.error("[orderUpdate]error:"+res.data.errMsg);
          }
        })
        .catch(function(e){
          console.log("[orderUpdate]"+e);
          self.$message.error("[orderUpdate]Network Error:"+e);
        })
    },
    orderNew:function(callback){
      let self = this;
      let data = this.getOrderData();
      data.append('token', this.token);

      this.axios
        .post('http://' + self.$global_msg.HOST + 'scripts/order/new.php', data)
        .then(function(res){
          if(res.data.status == 'success'){
            callback(res.data.id);
          }else{
            self.$message.error("[orderUpdate]error:"+res.data.errMsg);
          }
        })
        .catch(function(e){
          console.log("[orderUpdate]"+e);
          self.$message.error("[orderUpdate]Network Error:"+e);
        })
    },
    timeCheck:function(){
      if(!this.order.start_time){
        return {result:'fail',msg:''}
      }
      if(!this.order.name){
        return {result:'fail',msg:''}
      }
      if(this.order.step == ''){
        if (!this.order.end_time) {
          return {result:'fail',msg:''}
        }
        if(new Date(this.order.end_time) - new Date(this.order.start_time) < 0){
          return {result:'fail',msg:''}
        }
      }
      let suspend = false;
      if(this.processList.length > 0){
        let last_time = new Date(this.order.start_time);
        for(let i in this.processList){
          let p = this.processList[i]
          if(!p.time){
            return {result:'fail',msg:''}
          }
          if(p.mark == 'set_suspend'){
            if(suspend){
              return {result:'fail',msg:''}
            }
            suspend = true;
          }
          if(p.mark == 'unset_suspend'){
            if(!suspend){
              return {result:'fail',msg:''}
            }
            suspend = false;
          }
          let time = new Date(p.time);
          if(time - last_time < 0){
            return {result:'fail',msg:''}
          }
          last_time = time;
        }
        if(this.order.step == '' && new Date(this.order.end_time) - last_time < 0){
          return {result:'fail',msg:''}
        }
      }
      if(this.order.step != '' && suspend){
        return {result:'warning',suspend:suspend,msg:'?'}
      }
      if(this.order.step == '' && !suspend){
        return {result:'warning',suspend:suspend,msg:'?'}
      }
      if(this.order.step == '' && this.order.is_trouble == '1'){
        if(this.order.trouble_class == ''){
          return {result:'fail',msg:''}
        }
        if(this.order.trouble_reason == ''){
          return {result:'fail',msg:''}
        }
        let res = null;
        switch(this.order.trouble_class){
          case '':
          case '':
            if(!this.order.reasonDescription){
              res = {result:'fail',msg:''}
            }
            break;
          case '':
            if(!this.order.roomName){
              res =  {result:'fail',msg:''}
            }
            if(!this.order.roomType){
              res =  {result:'fail',msg:''}
            }
            if(!this.order.reasonDescription){
              res =  {result:'fail',msg:''}
            }
            break;
          case '':
            if(!this.order.roomName){
              res =  {result:'fail',msg:''}
            }
            if(!this.order.roomType){
              res =  {result:'fail',msg:''}
            }
            if(!this.order.reasonDescription){
              res =  {result:'fail',msg:''}
            }
            break;
        }
        if(res){
          return res;
        }
      }
      return {result:'success'};
    },
    submit: function() {
      let self = this;
      if (this.newOrder) {
        let check = this.timeCheck();
        switch (check.result) {
          case 'success':
            this.isLoading = true;
            this.orderNew(function(id) {
              self.$router.replace({
                name: 'orderEdit',
                query: {
                  id: id,
                  edit: false
                }
              })
              self.isLoading = false;
            });
            break;
          case 'warning':
            this.$confirm(check.msg, '', {
              type: 'info'
            }).then(function() {
              self.order.step = check.suspend ? '' : '';
              self.isLoading = true;
              self.orderNew(function(id) {
                self.$router.replace({
                  name: 'orderEdit',
                  query: {
                    id: id,
                    edit: false
                  }
                })
                self.isLoading = false;
              });
            }).catch(function() {})
            break;
          case 'fail':
            this.$message.error(check.msg);
            break;
        }
      } else {
        if (this.edit) {
          let check = this.timeCheck();
          switch (check.result) {
            case 'success':
              this.isLoading = true;
              this.updateProcessList(function() {
                self.orderUpdate(function() {
                  self.$router.replace({
                    name: 'orderEdit',
                    query: {
                      id: self.id,
                      edit: 'false'
                    }
                  })
                  self.isLoading = false;
                })
              });
              break;
            case 'warning':
              this.$confirm(check.msg, '', {
                type: 'info'
              }).then(function() {
                self.order.step = check.suspend ? '' : '';
                self.isLoading = true;
                self.updateProcessList(function() {
                  self.orderUpdate(function() {
                    self.$router.replace({
                      name: 'orderEdit',
                      query: {
                        id: self.id,
                        edit: 'false'
                      }
                    })
                    self.isLoading = false;
                  })
                });
              }).catch(function() {})
              break;
            case 'fail':
              this.$message.error(check.msg);
              break;
          }
        } else {
          self.$router.replace({
            name: 'orderEdit',
            query: {
              id: self.id,
              edit: 'true'
            }
          })
        }
      }
    },
    troubleClassChange:function(e){
      this.order.trouble_reason = '';
    },
    changeStep:function(){
      if(this.order.step != ''){
        this.order.end_time = '';
        this.changeTime();
      }
    },
    changeTime:function(e){
      if(this.timeCounter){
        clearTimeout(this.timeCounter);
      }
      if(this.order.start_time && this.order.end_time){
        this.order.time = ((new Date(this.order.end_time) - new Date(this.order.start_time))/60000).toFixed(0);
      }else if(this.order.start_time){
        this.order.time = ((new Date() - new Date(this.order.start_time))/60000).toFixed(0);
        this.timeCounter = setTimeout(this.changeTime,60000);
      }else{
        this.order.time = '';
      }
    },
    markFormatter:function(mark){
      switch(mark){
        case '':
          return '';
        case 'set_suspend':  
          return '';
        case 'unset_suspend':  
          return '';
      }
    },
    assess_query:async function(list){
      let self = this;
      for(let i in list){
        await this.canDoQuery(list[i]).then(function(res){
          self.canDo[list[i]] = res;
        })
      }
    },
    canDoQuery:async function(op){
      let data = new FormData();
      data.append('token', this.token);
      data.append('operation',op);
      let canDo = false;
      await this.axios
        .post('http://' + this.$global_msg.HOST + 'scripts/system/canDoQuery.php', data)
        .then(function(res) {
          if(res.data.status == 'success'){
            canDo = true;
          }else{
            console.log("can NOT do "+op+":"+res.data.errMsg);
          }
        })
        .catch(function(e){
          console.log("query error:"+e)
        })
      return canDo;
    }
  }
}
</script>

